



























.navbar nav{
  padding: 0 20px;
}
.navbar .container .left:first-child {
  padding: 0 30px;
}
.navbar img {
  height: 64px;
}


img {
  max-width: 100%;
}
body {
  font-family:'Helvetica Neue',Helvetica,'Segoe UI',Arial,sans-serif;
  font-size: 16px;
  background-color: #F1F1F1;
}
.index{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 30px;
  margin-top: 40px;
}
.index-header {
  grid-column: 1 / 4;
  background-image: url(/static/img/smartcity-index.06d11e4.jpg);
  background-size: cover;
  height: 400px;
}
.index h2{
  font-size: 1.8em;
  text-align: center;
  margin-top: 0;
}
.idea{
  color: white;
}


.project {
  max-width: 50%;
  margin: auto;
}
.project .time{
  display: block;
  font-size: 0.8em;
}


.new-project {
  max-width: 50%;
  margin-top: 40px;
}


.project-show {
  max-width: 50%;
  margin-top: 40px;
}


.about-header {
  background-image: url(/static/img/smartcity-about.22626df.jpg);
  background-size: cover;
  height: 300px;
  margin-top: 40px;
}
article {
  max-width: 50%;
  margin: auto;
}



.login{
  max-width: 25%;
  margin-top: 40px;
}
.login h2{
  font-size: 2.4em;
}
.login .field{
  margin-bottom: 16px;
}


.register{
  max-width: 25%;
  margin-top: 40px;
}
.register h2{
  font-size: 2.4em;
}
.register .field{
  margin-bottom: 16px;
}


.user-profile{
  max-width: 50%;
  margin-top: 40px;
}
.user-profile .time{
  display: block;
  font-size: 0.8em;
}


.prosjekter{
  max-width: 50%;
  margin-top: 40px;
}
.vis{
  display: block;
  padding: 4px 20px;
}

